import React from 'react'

import {
	PrivacyPolicyWrapper,
	PrivacyPolicyTitle,
	PrivacyPolicyParagraph,
	PrivacyPolicyLink,
	PrivacyPolicySubtitle,
	PrivacyPolicyList,
	PrivacyPolicyListItem,
	PrivacyPolicyListStrong
} from './styles'

const PrivacyPolicy = () => <PrivacyPolicyWrapper>
	<PrivacyPolicyTitle>Poltica Privacidade</PrivacyPolicyTitle>
	<PrivacyPolicyParagraph>A sua privacidade  importante para ns.  poltica do Casamento Leonardo e Jenyfer respeitar a sua privacidade em relao a qualquer informao sua que possamos coletar no site <PrivacyPolicyLink href="https://casamentoleoejeny.com.br">Casamento Leonardo e Jenyfer</PrivacyPolicyLink>, e outros sites que possumos e operamos.</PrivacyPolicyParagraph>
	<PrivacyPolicyParagraph>Solicitamos informaes pessoais apenas quando realmente precisamos delas para lhe fornecer um servio. Fazemo-lo por meios justos e legais, com o seu conhecimento e consentimento. Tambm informamos por que estamos coletando e como ser usado.</PrivacyPolicyParagraph>
	<PrivacyPolicyParagraph>Apenas retemos as informaes coletadas pelo tempo necessrio para fornecer o servio solicitado. Quando armazenamos dados, protegemos dentro de meios comercialmente aceitveis para evitar perdas e roubos, bem como acesso, divulgao, cpia, uso ou modificao no autorizados.</PrivacyPolicyParagraph>
	<PrivacyPolicyParagraph>No compartilhamos informaes de identificao pessoal publicamente ou com terceiros, exceto quando exigido por lei.</PrivacyPolicyParagraph>
	<PrivacyPolicyParagraph>O nosso site pode ter links para sites externos que no so operados por ns. Esteja ciente de que no temos controle sobre o contedo e prticas desses sites e no podemos aceitar responsabilidade por suas respectivas polticas de privacidade.</PrivacyPolicyParagraph>
	<PrivacyPolicyParagraph>Voc  livre para recusar a nossa solicitao de informaes pessoais, entendendo que talvez no possamos fornecer alguns dos servios desejados.</PrivacyPolicyParagraph>
	<PrivacyPolicyParagraph>O uso continuado de nosso site ser considerado como aceitao de nossas prticas em torno de privacidade e informaes pessoais. Se voc tiver alguma dvida sobre como lidamos com dados do usurio e informaes pessoais, entre em contacto connosco.</PrivacyPolicyParagraph>
	<PrivacyPolicySubtitle>Compromisso do Usurio</PrivacyPolicySubtitle>
	<PrivacyPolicyParagraph>O usurio se compromete a fazer uso adequado dos contedos e da informao que o Casamento Leonardo e Jenyfer oferece no site e com carter enunciativo, mas no limitativo:</PrivacyPolicyParagraph>
	<PrivacyPolicyList>
		<PrivacyPolicyListItem>A) No se envolver em atividades que sejam ilegais ou contrrias  boa f a  ordem pblica;</PrivacyPolicyListItem>
		<PrivacyPolicyListItem>B) No difundir propaganda ou contedo de natureza racista, xenofbica, ou sobre cassinos, casas de apostas (ex.: Bet.pt), jogos de sorte e azar, qualquer tipo de pornografia ilegal, de apologia ao terrorismo ou contra os direitos humanos;</PrivacyPolicyListItem>
		<PrivacyPolicyListItem>C) No causar danos aos sistemas fsicos (hardwares) e lgicos (softwares) do Casamento Leonardo e Jenyfer, de seus fornecedores ou terceiros, para introduzir ou disseminar vrus informticos ou quaisquer outros sistemas de hardware ou software que sejam capazes de causar danos anteriormente mencionados.</PrivacyPolicyListItem>
	</PrivacyPolicyList>
	<PrivacyPolicySubtitle>Mais informaes</PrivacyPolicySubtitle>
	<PrivacyPolicyParagraph>Esperemos que esteja esclarecido e, como mencionado anteriormente, se houver algo que voc no tem certeza se precisa ou no, geralmente  mais seguro deixar os cookies ativados, caso interaja com um dos recursos que voc usa em nosso site.</PrivacyPolicyParagraph>
	<PrivacyPolicyParagraph>Esta poltica  efetiva a partir de <PrivacyPolicyListStrong>Fevereiro/2021</PrivacyPolicyListStrong>.</PrivacyPolicyParagraph>
	<PrivacyPolicySubtitle>Para excluir seus dados</PrivacyPolicySubtitle>
	<PrivacyPolicyParagraph>Para excluir todos os dados armazenados vinculados ao seu e-mail, basta entrar em contato conosco.</PrivacyPolicyParagraph>
</PrivacyPolicyWrapper>

export default PrivacyPolicy